import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uniPopupDialog from '@/components/uni-popup/uni-popup-dialog.vue'
import { getToken } from '@/utils/auth'
export default {
	data() {
		return {
			buttonText:"冲吧,路飞~",
			isShow:true,
			resultData:{},
			isVerify:false,
		}
	},
	onLoad(){
		if( getToken()){
			//=> 获取用户信息,判断该项目是否已经参加
		}
		else{
			this.buttonText ="登录qu~";
		}
			
	},
	components: {  
		uniPopup,
		uniPopupDialog
	},
	methods:{
		/* 校验结果回调函数 */
		verifyResult(res){
			console.log(res);
			this.isVerify = true ;
			this.resultData = res;
		},
		/* 校验插件重置 */
		verifyReset(){
			this.$refs.verifyElement.reset();

			/* 删除当前页面的数据 */
			this.resultData = {};
		},
		close(done){
			done()
		},
		confirm(done){
			uni.navigateTo({
				url: '/pages/login/index?from=share'
			});
			done()
		},
		toLogin(){
			this.$refs.popup.open();
		},
		join(){
			console.log( "我加入会子了")
		}
	},
	watch:{
		buttonText(){
			this.isShow = false ,
			this.$refs.popup.open();
		}
		
	}
}
